* {
  box-sizing: border-box; }

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px; }

body {
  min-height: 100vh;
  padding: 0;
  margin: 0; }

main {
  display: block; }

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none; }

a {
  text-decoration: none; }

h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0; }
